let index = 0;

// 
export const initProxyRow = row => {
  index++;
  return {
    // Id
    id: index,
    // Id
    parentId: null,
    // 
    hover: false,
    // 
    checked: false,
    // 
    selected: false,
    // 
    rowExpand: false,
    // 
    hidden: false,
    // 
    isLeaf: true,
    // 
    nodeExpand: true,
    // 
    data: row
  };
};

// 
export const initProxyRows = rows => {
  return Array.from(rows, row => initProxyRow(row));
};

// 
export const initTreeProxyRows = (
  rows,
  treeNodeList = [],
  parentId = null,
  level = 0,
  index = 0
) => {
  rows.forEach(row => {
    let { children } = row;
    index++;
    let proxyRow = initProxyRow(row);
    proxyRow.id = index;
    proxyRow.level = level;
    proxyRow.parentId = parentId;

    treeNodeList.push(proxyRow);
    if (!Array.isArray(children) || children.length <= 0) return;
    proxyRow.isLeaf = false;
    initTreeProxyRows(
      children,
      treeNodeList,
      proxyRow.id,
      proxyRow.level + 1,
      index
    );
  });
  return treeNodeList;
};
